<template>
    <div id="wrap">
        <h3>Statistiques</h3>
        <select v-model="list">
            <option value="mostSuccessful">Plus réussis</option>
            <option value="mostFailed">Moins réussis</option>
        </select>
        <v-select
          :items="[{text:'Plus réussis', value: mostSuccessful},
                {text:'Moins réussis', value: mostFailed}]"
          v-model="list"
          label="Select"
          single-line>
          </v-select>
        <ul>
            <li v-for="word in list">
                <v-chip text-color="white" color="info">
                    {{ word | capitalize }}
                </v-chip>
                <v-btn fab dark small color="success">
                    {{ wordsStats[word][0] }}
                </v-btn>
                <v-btn fab dark small color="error">
                    {{ wordsStats[word][1] }}
                </v-btn>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'Stats',
    created(){
        this.wordsStats = JSON.parse(localStorage.getItem("words-stats"));
        this.list = this.mostSuccessful;
    },
    data(){
        return {
            wordsStats: {},
            list: [],
        }
    },
    computed: {
        mostFailed: function(){
            return Object.keys(this.wordsStats).sort((a, b) => {
                return this.wordsStats[b][1] - this.wordsStats[a][1];
            });
        },
        mostSuccessful: function(){
            return Object.keys(this.wordsStats).sort((a, b) => {
                return this.wordsStats[b][0] - this.wordsStats[a][0];
            });
        },
    },
}
</script>




