<template>
    <div id="wrap" v-touch:swipe="swipeHandler">
        <v-alert type="info" :value="true" v-show="!valid && !invalid">
            Glissez / swipez à gauche ou à droite pour répondre !
        </v-alert>
        <v-alert type="success" :value="true" v-show="valid && !end">
            Correct !
        </v-alert>
        <v-alert type="error" :value="true" v-show="invalid && !end">
            Nop :(
        </v-alert>
        <v-progress-circular
          :size="100"
          :width="15"
          :rotate="360"
          :value="progression"
          color="teal"
          id="progress"
        >
          {{ wordIndex }}
        </v-progress-circular>
        <div v-if="!end">
            <p>Score: {{ score }}</p>
            <p>{{ word.word | upper }}</p>
            <div>
                <v-btn fab dark large color="teal" @click="checkGender('M')">
                    M
                </v-btn>
                <v-btn fab dark large color="teal" @click="checkGender('F')">
                    F
                </v-btn>
            </div>
        </div>
        <div id="details" v-show="end">
            <p>Score: {{ score }} / {{ words.length }}</p>
            <p v-show="failed.length">Mots ratés:</p>
            <ul>
                <li v-for="word in failed">
                    <v-chip color="warning">{{ word }}</v-chip>
                </li>
            </ul>
            <v-btn round color="teal" large @click="reStart">
                Rejouer ?
            </v-btn>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Words',
    beforeMount(){
        this.selectWords();
    },
    created(){
        if(!localStorage.getItem("words-stats")){
            localStorage.setItem("words-stats", "{}");
        }
    },
    data(){
        return {
            wordsList: [
                [{word: 'angle', gender: 'M'},
                {word: 'armoire', gender: 'F'},
                {word: 'banc', gender: 'M'}, 
                {word: 'bureau', gender: 'M'}, 
                {word: 'cabinet', gender: 'M'}, 
                {word: 'carreau', gender: 'M'}, 
                {word: 'chaise', gender: 'F'}, 
                {word: 'classe', gender: 'F'}, 
                {word: 'clé', gender: 'F'}, 
                {word: 'coin', gender: 'M'}],
                [{word: 'crayon', gender: 'M'},
                {word: 'stylo', gender: 'M'},
                {word: 'feutre', gender: 'M'},
                {word: 'pointe', gender: 'F'},
                {word: 'mine', gender: 'F'},
                {word: 'gomme', gender: 'F'},
                {word: 'dessin', gender: 'M'},
                {word: 'coloriage', gender: 'M'},
                {word: 'rayure', gender: 'F'},
                {word: 'peinture', gender: 'F'}],
                [{word: 'arrosoir', gender: 'M'},
                {word: 'assiette', gender: 'F'},
                {word: 'balle', gender: 'F'},
                {word: 'bateau', gender: 'M'},
                {word: 'boîte', gender: 'F'},
                {word: 'bouchon', gender: 'M'},
                {word: 'bouteille', gender: 'F'},
                {word: 'bulle', gender: 'F'},
                {word: 'canard', gender: 'M'},
                {word: 'casserole', gender: 'F'}],
                [{word: 'aiguille', gender: 'F'},
                {word: 'ampoule', gender: 'F'},
                {word: 'avion', gender: 'M'},
                {word: 'bois', gender: 'M'},
                {word: 'bout', gender: 'M'},
                {word: 'bricolage', gender: 'M'},
                {word: 'bruit', gender: 'M'},
                {word: 'cabane', gender: 'F'},
                {word: 'carton', gender: 'M'},
                {word: 'clou', gender: 'M'}],
                [{word: "accident", gender: "M"},
                {word: "aéroport", gender: "M"},
                {word: "auto", gender: "F"},
                {word: "camion", gender: "M"},
                {word: "engin", gender: "M"},
                {word: "feu", gender: "M"},
                {word: "frein", gender: "M"},
                {word: "fusée", gender: "F"},
                {word: "garage", gender: "M"},
                {word: "gare", gender: "M"}],
                [{word: "bagarre", gender: "F"},
                {word: "balançoire", gender: "F"},
                {word: "ballon", gender: "M"},
                {word: "bande", gender: "F"},
                {word: "bicyclette", gender: "F"},
                {word: "bille", gender: "F"},
                {word: "cadenas", gender: "M"},
                {word: "cage à écureuil", gender: "F"},
                {word: "cerf-volant", gender: "M"},
                {word: "château", gender: "M"}],
            ],
            words: [],
            wordIndex: 0,
            score: 0,
            failed: [],
            end: false,
            valid: false,
            invalid: false,
        }
    },
    computed: {
        word: function(){
            return this.words[this.wordIndex]
        },
        progression: function(){
            return this.wordIndex * 100 / this.words.length;
        },
    },
    methods: {
        checkGender(gender){
            if(gender === this.word.gender){
                this.score++;
                this.valid = true;
                this.invalid = false;
            } else {
                this.failed.push(this.word.word);
                this.valid = false;
                this.invalid = true;
            }
            this.setStats(this.word.word, this.invalid);
            this.wordIndex++;
        },
        reStart(){
            this.selectWords();
            this.wordIndex = 0;
            this.failed = [];
            this.score = 0;
            this.valid = false;
            this.invalid = false;
        },
        selectWords(){
            this.words = this.wordsList[Math.floor((Math.random() * this.wordsList.length))];
        },
        setStats(word, failed){
            let wordStats = JSON.parse(localStorage.getItem("words-stats"));
            if(word in wordStats){
                wordStats[word][failed | 0]++;
            } else {
                wordStats[word] = failed ? [0, 1] : [1, 0];
            }
            localStorage.setItem("words-stats", JSON.stringify(wordStats));
        },
        swipeHandler(action){
            this.checkGender(["M", "F"][action === "swiperight" | 0]);
        },
    },
    watch: {
        wordIndex: function(val, oldVal){
            this.end = val === this.words.length;
        }
    },
}
</script>

<style scoped>
#wrap {
    background-color: #616161;
}
</style>


