import Vue from 'vue'
import Router from 'vue-router'
import Words from '@/components/Words'
import Stats from '@/components/Stats'

Vue.use(Router)

export default new Router({
	routes: [
	{
		path: '/',
		name: 'Words',
		component: Words
	},
	{
		path: '/stats',
		name: 'Stats',
		component: Stats
	},
	]
})



